.carousel__container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;

    @media (max-width: 700px) {
        grid-template-columns: none;
        grid-template-rows: 1fr;
    }
}

.carousel__card {
    display: flex;
    flex-direction: column;

    border-radius: 1rem;
    padding: 20% 10%;
    line-height: 2rem;
    gap: 2rem;

    text-align: center;
    /*background-color: #efefef;*/
    background-color: var(--bgColorCardCarousel);

    @media (max-width: 500px) {
        padding: 2rem;
    }
}

.carousel__card blockquote {
    width: 80%;
    margin: 0 auto;

    /*color: #000;*/
    font-size: 1.5rem;
}

.carousel__text {
    text-align: center;
}
.carousel__caption {
    text-align: center;
}